import React, { useState, useEffect } from "react";

// Show all people
export default function StarWars() {
  let [people, setPeople] = useState([]); // start with an empty list of zero people

  useEffect(() => {
    fetch("https://swapi.dev/api/people", { method: "GET", cache: "default" })
      .then((response) => response.json())
      .then((responseBody) => setPeople(responseBody.results));
    return () => {};
  }, []);

  return (
    <div>
      <h2>Star wars!</h2>
      <ul>
        {people.map((onePerson) => (
          <Person key={onePerson.url} person={onePerson} />
        ))}
      </ul>
    </div>
  );
}

// Show one person
function Person({ person }) {
  // Any element created within a `.map` (or similar) must have a key.
  // https://react.dev/learn/rendering-lists#rules-of-keys
  return (
    <li key={person.url}>
      <button>{person.name}</button>
      <ul>
        <li>Height: {person.height}cm</li>
        <li>Mass: {person.mass}kg</li>
        <li>Born: {person.birth_year}</li>
        <li>
          Homeworld: <Planet url={person.homeworld}></Planet>
        </li>
      </ul>
    </li>
  );
}

// Show one Planet
function Planet({ url }) {
  let [planet, setPlanet] = useState(undefined);

  useEffect(
    () => {
      fetch(url, {
        method: "GET", // of course, POST, PUT, DELETE are also available. See https://developer.mozilla.org/en-US/docs/Web/API/Fetch_API/Using_Fetch#uploading_json_data
        cache: "default",
      })
        .then((response) => response.json())
        .then((responseBody) => setPlanet(responseBody));
      return () => {};
    },
    [] // invoke as soon as the HomeWorld component is instantiated.
  );

  if (!planet) return <span>{url}</span>;
  else
    return (
      <ul>
        <li>name: {planet.name}</li>
        <li>gravity: {planet.gravity}</li>
      </ul>
    );
}
