import React, { useState, useEffect } from "react";

import FlowChart from "./FlowChart.jsx";
import RedGreenArray from "./RedGreenArray.jsx";
import statesWrapper from "./statesWrapper.js";

function fix34(input) {
  const FINISHED = "FINISHED";
  const START = "START";

  let data = { input: [...input], i3: -1, i4: -1, blue: -1 };
  let step = START;

  const FIND_FIRST_4 = "FIND_FIRST_4";
  const FIND_FIRST_3 = "FIND_FIRST_3";
  const CHECK_WHILE_CONDITION = "CHECK_WHILE_CONDITION";
  const CHECK_IF_SWAP_NEEDED = "CHECK_IF_SWAP_NEEDED";
  const SWAP_NEEDED = "SWAP_NEEDED";
  const ADVANCE_3 = "ADVANCE_3";
  const ADVANCE_4 = "ADVANCE_4";

  function* iter() {
    yield FIND_FIRST_4;
    data.i4 = 0;
    while (data.i4 < data.input.length && data.input[data.i4] != 4) data.i4++;

    yield FIND_FIRST_3;
    data.i3 = 0;
    while (data.i3 < data.input.length && data.input[data.i3] != 3) data.i3++;

    while ((yield CHECK_WHILE_CONDITION) && data.i3 < data.input.length && data.i4 < data.input.length) {
      yield CHECK_IF_SWAP_NEEDED;
      if (data.input[data.i3 + 1] != 4) {
        yield SWAP_NEEDED;
        let temp = data.input[data.i3 + 1];
        data.input[data.i3 + 1] = data.input[data.i4];
        data.input[data.i4] = temp;
      }

      yield ADVANCE_4;
      data.i4++;
      while (data.i4 < data.input.length && data.input[data.i4] != 4) data.i4++;

      yield ADVANCE_3;
      data.i3++;
      while (data.i3 < data.input.length && data.input[data.i3] != 3) data.i3++;
    }
    yield FINISHED;
  }

  let runner = iter();

  let result = {
    getSource: () => iter.toString(),
    getData: () => ({ data: data, step: step }),
    next: (...params) => {
      step = runner.next(...params);
    },
  };

  return result;
}

export default function Coroutine() {
  let [algorithm, setAlgorithm] = useState(statesWrapper(fix34([5, 3, 5, 4, 3, 4, 5, 4, 5, 4, 3, 5, 3, 5])));
  let [cycle, setCycle] = useState(1);
  let [states, setStates] = useState([]);
  let [source, setSource] = useState("");

  useEffect(() => setStates(algorithm.getStates(cycle)), [cycle, algorithm]);
  useEffect(() => setSource(algorithm.getSource()), [algorithm]);

  const tick = (direction) => setCycle((cycle) => cycle + direction);
  const onWheel = (e) => {
    if (e.deltaY > 0) tick(1);
    else if (e.deltaY < 0) tick(-1);
  };

  return (
    <>
      <div onWheel={onWheel}>
        <details>
          <summary>
            CodingBat <a href="https://codingbat.com/prob/p159339">fix34</a>
          </summary>
          <pre> {source} </pre>
        </details>
        {states.length && <button onClick={() => tick(1)}>{states.slice(-1)[0].step.value}</button>}
        <br></br>
        <FlowChart states={states} />
      </div>
      <table border="1">
        <tbody>
          {states.map((state, index) => (
            <RedGreenArray
              key={index}
              step={state.step.value}
              data={state.data.input}
              redIndex={state.data.i3}
              greenIndex={state.data.i4}
            />
          ))}
        </tbody>
      </table>
    </>
  );
}
