import React, { useState } from "react";
import Mermaid from "./Mermaid.jsx";

export default function FlowChart({ states }) {
  let [horizontal, setHorizontal] = useState(true);

  const toggleRotate = () => setHorizontal((b) => !b);

  const diagram = () => {
    let paths = ["graph " + (horizontal ? "LR" : "TB")];
    let priorNode = undefined;
    states.forEach((state, index) => {
      let node = state.step.value.replaceAll(" ", "_");
      if (priorNode) paths.push(` ${priorNode}-->|"${index}"|${node}`);
      priorNode = node;
    });
    return paths.join("\n");
  };

  return (
    <>
      {false && <button onClick={toggleRotate}>Rotate</button>}
      {states.length > 1 && <Mermaid chart={diagram()} />}
    </>
  );
}
