import React, { useState, useEffect } from "react";

import FlowChart from "./FlowChart.jsx";
import RedGreenArray from "./RedGreenArray.jsx";
import statesWrapper from "./statesWrapper.js";

function shiftMerge(input1, input2) {
  let data = { input1: [...input1], input2: [...input2], output: [] };
  let step = "START";

  function* iter() {
    while ((yield "CHECK BOTH NONEMPTY") && data.input1.length > 0 && data.input2.length > 0) {
      yield "CHECK WHICH SMALLER";
      if (data.input1[0] < data.input2[0]) {
        yield "FIRST SMALLER";
        data.output.push(data.input1.shift());
      } else {
        yield "SECOND SMALLER";
        data.output.push(data.input2.shift());
      }
    }

    while ((yield "CHECK FIRST NONEMPTY") && data.input1.length > 0) {
      yield "TRANSFERRING FROM FIRST";
      data.output.push(data.input1.shift());
    }

    while ((yield "CHECK SECOND NONEMPTY") && data.input2.length > 0) {
      yield "TRANSFERRING FROM SECOND";
      data.output.push(data.input2.shift());
    }

    yield "FINISHED";
  }

  let runner = iter();

  let result = {
    getSource: () => iter.toString(),
    getData: () => ({ data: data, step: step }),
    next: (...params) => {
      step = runner.next(...params);
    },
  };

  return result;
}

export default function Coroutine() {
  let [algorithm, setAlgorithm] = useState(
    statesWrapper(shiftMerge([1, 3, 4, 7, 10, 12, 20, 21, 30, 40], [2, 4, 11, 12, 19, 20, 21, 25]))
  );
  let [cycle, setCycle] = useState(1);
  let [states, setStates] = useState([]);
  let [source, setSource] = useState("");

  useEffect(() => setStates(algorithm.getStates(cycle)), [cycle, algorithm]);
  useEffect(() => setSource(algorithm.getSource()), [algorithm]);

  const tick = (direction) => setCycle((cycle) => cycle + direction);
  const onWheel = (e) => {
    if (e.deltaY > 0) tick(1);
    else if (e.deltaY < 0) tick(-1);
  };

  return (
    <>
      <div onWheel={onWheel}>
        <details>
          <summary>
            CodingBat <a href="https://codingbat.com/prob/p159339">fix34</a>
          </summary>
          <pre> {source} </pre>
        </details>
        {states.length && <button onClick={() => tick(1)}>{states.slice(-1)[0].step.value}</button>}
        <br></br>
        <div className="horizontalFlex">
          <FlowChart states={states} />
          <table border="1">
            <tbody>
              {states.map((state, index) => (
                <RedGreenArray key={index} step={state.step.value} data={state.data.input1} />
              ))}
            </tbody>
          </table>
          <table border="1">
            <tbody>
              {states.map((state, index) => (
                <RedGreenArray key={index} step={""} data={state.data.input2} />
              ))}
            </tbody>
          </table>
          <table border="1">
            <tbody>
              {states.map((state, index) => (
                <RedGreenArray key={index} step={""} data={state.data.output} />
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </>
  );
}
