import React, { useState } from "react";

import Mermaid from "./Mermaid.jsx";

const FIND_FIRST_4 = "FIND_FIRST_4";
const FIND_FIRST_3 = "FIND_FIRST_3";
const CHECK_WHILE_CONDITION = "CHECK_WHILE_CONDITION";
const CHECK_IF_SWAP_NEEDED = "CHECK_IF_SWAP_NEEDED";
const SWAP_NEEDED = "SWAP_NEEDED";
const FINISHED = "FINISHED";
const ADVANCE_3 = "ADVANCE_3";
const ADVANCE_4 = "ADVANCE_4";

const sampleAnswer = `
public int[] fix34(int[] nums) {
    int index3, index4, temp;
    index4 = 0; while (index4 < nums.length && nums[index4] != 4) index4++;
    index3 = 0; while (index3 < nums.length && nums[index3] != 3) index3++;

    while (index3 < nums.length && index4 < nums.length) {
      if (nums[index3 + 1] != 4) {
        temp = nums[index3+1];
        nums[index3+1] = nums[index4];
        nums[index4] = temp;
      }
      index4++; while (index4 < nums.length && nums[index4] != 4) index4++;
      index3++; while (index3 < nums.length && nums[index3] != 3) index3++;
    }
    return nums;
}
`;

const steps = {};
{
  steps[FIND_FIRST_4] = (state) => {
    let i = state.i4;
    while (i < state.input.length && state.input[i] != 4) {
      i++;
    }
    state.i4 = i;
    state.step = FIND_FIRST_3;
    return state;
  };

  steps[FIND_FIRST_3] = (state) => {
    let i = state.i3;
    while (i < state.input.length && state.input[i] != 3) {
      i++;
    }
    state.i3 = i;
    state.step = CHECK_WHILE_CONDITION;
    return state;
  };

  steps[CHECK_WHILE_CONDITION] = (state) => {
    if (state.i3 < state.input.length && state.i4 < state.input.length) {
      state.blue = state.i3 + 1;
      state.step = CHECK_IF_SWAP_NEEDED;
    } else {
      state.step = FINISHED;
    }
    return state;
  };

  steps[CHECK_IF_SWAP_NEEDED] = (state) => {
    if (state.input[state.i3 + 1] != 4) {
      state.step = SWAP_NEEDED;
    } else {
      state.step = ADVANCE_4;
      state.blue = -1;
    }
    return state;
  };

  steps[SWAP_NEEDED] = (state) => {
    let temp = state.input[state.i3 + 1];
    state.input[state.i3 + 1] = state.input[state.i4];
    state.input[state.i4] = temp;
    state.blue = -1;
    state.step = ADVANCE_4;
    return state;
  };

  steps[ADVANCE_4] = (state) => {
    let i = state.i4 + 1;
    while (i < state.input.length && state.input[i] != 4) {
      i++;
    }
    state.i4 = i;
    state.step = ADVANCE_3;
    return state;
  };

  steps[ADVANCE_3] = (state) => {
    let i = state.i3 + 1;
    while (i < state.input.length && state.input[i] != 3) {
      i++;
    }
    state.i3 = i;
    state.step = CHECK_WHILE_CONDITION;
    return state;
  };

  steps[FINISHED] = (state) => 0;
}

export default function ThreeFour() {
  let [states, setStates] = useState([
    {
      input: [5, 3, 5, 4, 3, 4, 5, 4, 5, 4, 3, 5, 3, 5],
      i3: -1,
      i4: -1,
      blue: -1,
      step: FIND_FIRST_4,
    },
  ]);

  const tick = () =>
    setStates((states) => {
      const state = states.slice(-1)[0];
      const nextState = steps[state.step](JSON.parse(JSON.stringify(state)));
      if (!nextState) return states;
      return [...states, nextState];
    });

  const untick = () =>
    setStates((states) => {
      let newStates = [...states];
      if (newStates.length > 1) newStates.pop();
      return newStates;
    });

  const reset = () => setStates((states) => [states[0]]);

  const diagram = () => {
    let paths = ["graph LR"];
    let priorNode = "START";
    states.forEach((state, index) => {
      let node = state.step;
      paths.push(` ${priorNode}-->|"${index}"|${node}`);
      priorNode = node;
    });
    return paths.join("\n");
  };

  const reversedStates = () => {
    let reversed = [...states];
    reversed.reverse();
    return reversed;
  };

  const onWheel = (e) => {
    if (e.deltaY > 0) tick();
    else if (e.deltaY < 0) untick();
  };

  return (
    <>
      <div onWheel={onWheel}>
        <details>
          <summary>
            CodingBat <a href="https://codingbat.com/prob/p159339">fix34</a>
          </summary>
          <pre>{sampleAnswer}</pre>
        </details>
        <button onClick={reset}>RESTART</button>
        <button onClick={untick}>BACK</button>
        <button onClick={tick}>{states.slice(-1)[0].step}</button>
        <br></br>
        <pre>{steps[states.slice(-1)[0].step].toString().replaceAll(/state\./g, "")}</pre>
        <Mermaid chart={diagram()} />
      </div>
      <table border="1">
        <tbody>
          {reversedStates().map((state, index) => (
            <RedGreenArray
              key={index}
              step={state.step}
              data={state.input}
              redIndex={state.i3}
              greenIndex={state.i4}
              blueIndex={state.blue}
            />
          ))}
        </tbody>
      </table>
    </>
  );
}

function RedGreenArray({ data, step, redIndex, greenIndex, blueIndex }) {
  function computeClass(index) {
    if (index == redIndex && index == greenIndex) return "redgreen";
    if (index == redIndex) return "red";
    if (index == greenIndex) return "green";
    if (index == blueIndex) return "blue";
    return "";
  }

  return (
    <tr>
      <td>{step}</td>
      {data.map((val, index) => (
        <td className={computeClass(index)} key={index}>
          {val}
        </td>
      ))}
    </tr>
  );
}
