import React from "react";
import { createRoot } from "react-dom/client";
import { BrowserRouter, Routes, Route, Outlet, Link } from "react-router-dom";
import "../css/styles.css";

import StarWars from "./StarWars.jsx";
import Game from "./Game.jsx";
import ThreeFour from "./ThreeFour.jsx";
import Coroutine from "./Coroutine.jsx";
import ShiftMerge from "./ShiftMerge.jsx";
import IndexMerge from "./IndexMerge.jsx";

function Layout() {
  return (
    <>
      <nav>
        <Link to="/">Main</Link>
        <Link to="34yield">34 using yield</Link>
        <Link to="shiftMerge">Merging arrays using shift</Link>
        <Link to="indexMerge">Merging arrays using indices</Link>
        <Link to="starwars">StarWars</Link>
        <Link to="game">Play</Link>
        <Link to="34">Three Four Game</Link>
      </nav>

      <Outlet />
    </>
  );
}

function Main() {
  return (
    <React.StrictMode>
      <BrowserRouter>
        <Routes>
          <Route path="/app3?/src?/main?/resources?/static?/index.html?" element={<Layout />}>
            <Route path="34yield" element={<Coroutine />} />
            <Route path="shiftMerge" element={<ShiftMerge />} />
            <Route path="indexMerge" element={<IndexMerge />} />
            <Route path="starwars" element={<StarWars />} />
            <Route path="game" element={<Game />} />
            <Route path="34" element={<ThreeFour />} />
          </Route>
        </Routes>
      </BrowserRouter>
    </React.StrictMode>
  );
}

createRoot(document.getElementById("react-mountpoint")).render(<Main />);
