import React, { useState } from "react";

export default function Game() {
  const [game, setGame] = useState({ isChecked: false, counter: 0, name: "Marshall" });

  const updateGame = () => setGame(g => ({ ...g, isChecked: !g.isChecked, counter: g.counter + 1 }));

  return (
    <div>
      <h1>{game.isChecked ? "Checked?" : "Unchecked?"} </h1>
      <button onClick={updateGame}>Click me!</button>
      <button disabled={game.isChecked} onClick={updateGame}>
        Perhaps disabled
      </button>
      <div>{game.counter}</div>
      <HappySad happy={game.isChecked} />
    </div>
  );
}

function HappySad({ happy }) {
  return <div>{happy ? ":-)" : ":-("}</div>;
}
